import { useState, useEffect } from "react"
import { supabase } from "../lib/supabase"
import { trackVisitor } from "../lib/trackVisitor"

export const useVisitorCount = () => {
  const [todayCount, setTodayCount] = useState(null)
  const [totalCount, setTotalCount] = useState(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    if (!supabase) {
      setLoading(false)
      return
    }

    const fetchVisitors = async () => {
      try {
        // ì¡°íì ì¶ì  (ì´ë¯¸ ì¶ì ëìì¼ë©´ ìë¬´ê²ë íì§ ìì)
        await trackVisitor()

        // ì¤ë ë ì§ (YYYY-MM-DD íì)
        const today = new Date().toISOString().split("T")[0]

        // ì¤ë ë°©ë¬¸ìì ì¡°í
        const { count: todayCount, error: todayError } = await supabase
          .from("visits")
          .select("*", { count: "exact", head: true })
          .eq("visit_date", today)

        if (todayError) throw todayError

        // ì ì²´ ë°©ë¬¸ìì ì¡°í
        const { count: totalCount, error: totalError } = await supabase
          .from("visits")
          .select("*", { count: "exact", head: true })

        if (totalError) throw totalError

        setTodayCount(todayCount || 0)
        setTotalCount(totalCount || 0)
      } catch (error) {
        console.error("Error fetching visitors:", error)
        // ìë¬ ë°ì ì ê¸°ë³¸ê° ì¤ì 
        setTodayCount(0)
        setTotalCount(0)
      } finally {
        setLoading(false)
      }
    }

    fetchVisitors()
  }, [])

  // ì«ìë¥¼ í¬ë§·í (ì: 1234 -> "1.2K")
  const formatCount = count => {
    if (count === null || count === undefined) return "0"
    if (count < 1000) return count.toString()
    if (count < 1000000) return `${(count / 1000).toFixed(1)}K`
    return `${(count / 1000000).toFixed(1)}M`
  }

  return {
    todayCount,
    totalCount,
    formattedToday: formatCount(todayCount),
    formattedTotal: formatCount(totalCount),
    loading,
  }
}
